<script>
import Vue from "vue";
import { Field, Button, Loading, Overlay } from "vant";

import firebase from "firebase/app";
import "firebase/auth";
import { firebaseConfig } from "@/common/config";

import { mapActions } from "vuex";
import * as Server from "../server";

try {
  firebase.initializeApp(firebaseConfig);
} catch (error) {
  console.log(
    "FirebaseInitError: ",
    firebase.initializeApp(firebaseConfig, "login")
  );
}

Vue.use(Field)
  .use(Button)
  .use(Loading)
  .use(Overlay)
  .use(Loading);
export default {
  name: "login",
  data() {
    return {
      showManulLogin: false,
      email: "",
      password: "",
      showPass: false,
      showLoginOverlay: false,
      oldUser: JSON.parse(localStorage.getItem("user") || "{}")
    };
  },
  created() {
    console.log("Region => ", firebase.auth().languageCode);

    firebase.auth().onAuthStateChanged(user => {
      if (user) localStorage.setItem("user", JSON.stringify(user));
      console.log("Firebase User => ", user);
    });

    if (this.showLoginPage()) {
      this.showManulLogin = true;
      return;
    }

    this.showManulLogin = false;
    this.loginByAnonymous();

    // setTimeout(() => {
    //   window.location.reload();
    // }, 15 * 1000);
  },
  methods: {
    ...mapActions(["login", "logout"]),
    showLoginPage() {
      let url = window.location.href;
      // let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      let result =
        /inTest/gi.test(url) || /manul/gi.test(url) || /fromHome/gi.test(url);
      return result;
    },
    async tracking() {
      let profile = JSON.parse(localStorage.getItem("profile") || "{}");
      let trackInfo = JSON.parse(localStorage.getItem("trackInfo") || "{}");
      if (!profile.uid || !trackInfo.source) {
        console.log("no trackinfo => ", profile, trackInfo);
        return;
      }
      if (this.oldUser.isAnonymous) {
        console.log("Already Tracking, return");
        return;
      }
      let { status, data } = await Server.TrackSourceFrom(
        profile.uid,
        trackInfo.source,
        trackInfo.refby
      );
      console.log("Tracking: ", status, " => ", data);
    },
    async loginSuccess() {
      localStorage.setItem("isManulLogin", this.showManulLogin);
      this.$router.replace("/home?fromLogin");
      await this.tracking();
      this.showLoginOverlay = false;
      // window.location.reload();
    },
    loginByAnonymous() {
      let isUpdate = this.oldUser.isAnonymous; // 判断是否已匿名登录过
      if (isUpdate) {
        console.log("Alredy Anonymous, to home.");
        localStorage.setItem("isManulLogin", this.showManulLogin);
        this.$router.replace("/home");
        // window.location.reload();
        // this.$toast("Login Success");
        return;
      }
      if (this.showManulLogin) this.showLoginOverlay = true;
      firebase
        .auth()
        .signInAnonymously()
        .then(async res => {
          console.log("signInAnonymously", res.user);
          let user = res.user;
          await this.login({
            user,
            method: { type: "anonymous", id: "" },
            isUpdate
          });
          await this.loginSuccess();
          // window.location.reload();
        })
        .catch(error => {
          this.showLoginOverlay = false;
          this.$toast(error.message);
        });
    },
    loginByEmail() {
      if (!this.email) {
        this.$toast("Please input email");
        return;
      }
      if (!this.password) {
        this.$toast("Please input password");
        return;
      }
      let isUpdate = this.oldUser.isAnonymous; // 判断是否已匿名登录过
      this.showLoginOverlay = true;
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(async res => {
          const { user } = res;
          await this.login({
            user,
            method: { type: "email", id: this.email },
            isUpdate
          });
          await this.loginSuccess();
        })
        .catch(error => {
          console.log(error);
          if (error.code === "auth/user-not-found") {
            this.createdUserByEmail();
          } else {
            this.showLoginOverlay = false;
            this.$toast(error.message);
          }
        });
    },
    createdUserByEmail() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(res => {
          console.log("createUserWithEmailAndPassword => ", res);
          this.loginByEmail();
        })
        .catch(error => {
          console.log(error);
          this.$toast(error.message);
        });
    },
    loginByGoogle() {
      let isUpdate = this.oldUser.isAnonymous; // 判断是否已匿名登录过
      this.showLoginOverlay = true;
      console.log("Login by google");
      const provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(async res => {
          console.log(res);
          const { user } = res;
          await this.login({
            user,
            method: { type: "google", id: "" },
            isUpdate
          });
          await this.loginSuccess();
        })
        .catch(error => {
          console.log(error);
          this.showLoginOverlay = false;
          this.$toast(error.message);
        });
    },
    loginByFacebook() {
      let isUpdate = this.oldUser.isAnonymous; // 判断是否已匿名登录过
      this.showLoginOverlay = true;
      const provider = new firebase.auth.FacebookAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(async res => {
          console.log(res);
          const { user } = res;
          await this.login({
            user,
            method: { type: "facebook", id: "" },
            isUpdate
          });
          await this.loginSuccess();
        })
        .catch(error => {
          this.showLoginOverlay = false;
          this.$toast(error.message);
        });
    },
    toPhoneLogin() {
      this.$router.push("/phone-login");
    }
  }
};
</script>

<style lang="less">
@keyframes loaderAnimal {
