      & > * {
        width: 100%;
      }
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    h1 {
      text-align: center;
      line-height: 60px;
      font-size: 32px;
      margin-bottom: 100px;
      color: red;
      background-image: linear-gradient(45deg, purple, red);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: rgba(0, 0, 0, 0);
    }
    .methods {
      * {
        transition: all 0.4s ease-in-out;
      }
      & > * + * {
        margin-top: 40px;
      }
      .method {
        background: #fbfbfb;
        // box-shadow: 1px 2px 3px 0 #d4d0d0;
        cursor: pointer;
        display: flex;
        align-items: center;
        height: 60px;
        border-radius: 3px;
        .icon {
          margin: 0 20px;
          width: 24px;
          height: 24px;
          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;
        }
        .title {
          font-size: 18px;
        }
      }
    }
  }
  /** new-login */

  .login-tips {
    text-align: right;
    font-size: 12px;
    color: #cfcfcf;
    padding: 10px 0;
  }
  .login-button-wrap {
    margin-top: 40px;
    text-align: center;
    // padding: 20px;
    .login-btn {
      height: 36px;
      line-height: 1;
      font-size: 18px;
      font-weight: bold;
      width: 270px;
      text-align: center;
    }
    .login-btn + .login-btn {
      margin-top: 20px;
    }
  }
  .login-tools {
    text-align: center;
    padding: 60px 0;

    .login-tool {
      display: flex;
      align-items: center;
      justify-content: center;
      &-item {
        background: #ddd;
        width: 47px;
        height: 47px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;

        & + .login-tool-item {
          margin-left: 15px;
        }
      }
    }

    .tools-tips {
      font-size: 12px;
      color: #cfcfcf;
      line-height: 16px;
      margin-top: 15px;
    }
  }
  .login-overlay {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
</style>
